<template>
  <v-row class="px-4">
    <v-col
      cols="12"
      class="text-center text-h4"
    >
      Авторизация
    </v-col>

    <v-col class="d-flex justify-center">
      <v-card
        flat
        color="transparent"
        width="400"
      >
        <v-row>
          <v-col
            cols="12"
            class="pb-2"
          >
            <v-form>
              <v-text-field
                v-model="username"
                outlined
                hide-details="auto"
                label="Логин"
                class="rounded-lg mb-5"
              />
              <v-text-field
                v-model="password"
                outlined
                :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                :type="show ? 'text' : 'password'"
                hide-details="auto"
                label="Пароль"
                class="rounded-lg mb-5"
                @click:append="show = !show"
              />
            </v-form>
          </v-col>

          <v-col>
            <v-btn
              :disabled="!username || !password"
              block
              depressed
              color="primary"
              class="text-none rounded-lg"
              x-large
              @click="login"
            >
              Войти
            </v-btn>
          </v-col>
        </v-row>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      username: '',
      password: '',
      show: false,
    };
  },
  methods: {
    login() {
      this.$router.push('/users');
    },
  },
};
</script>
